import { motion } from "framer-motion";
import axios from "axios"
import { useContext } from "react";
import { Link } from "react-router-dom";
import { HaschconnectContext } from "../../App";
import { AccountId, TransactionId, TransactionReceipt, TransferTransaction, Hbar, TokenAssociateTransaction } from "@hashgraph/sdk";


// const tokenId = "0.0.5711914"
const ownId = "0.0.5711848"

const uri = `https://testnet.mirrornode.hedera.com/api/v1`;
const instance = axios.create({
  baseURL: uri,
});



const NftSingle = ({ name, image, accountId, topic, cid, price, serialNumber, tokenId }) => {
  const { hash, minted, setMinted, weaponNfts } = useContext(HaschconnectContext);
  let nfts = weaponNfts;

  console.log(accountId)

  const Sendnft = (metadata) => {
    var arr = {
      accountId: accountId.toString(),
      metadata: metadata,
    };
    if (metadata.length > 0) {
      axios.post("/users/nftsave", arr).then((res) => {
        if (res.data === "success") alert("success");
        else if (res.data === "Already send") alert("Already send");
      });
    }
  };

  async function isNFTAssociatedWithWallet(tokenId, accountId) {
    let check = false
    try {
      let balance = await instance.get(`balances?account.id=${accountId}`);
      const tokens = balance.data.balances[0].tokens;
      for (var i = 0; i < tokens.length; i++) {
        if (tokens[i].token_id === tokenId) check = true

      }
      return check
    } catch (err) {
    }
  }

  async function handleClickMint(tokenId, cid) {
    const isAssociated = await isNFTAssociatedWithWallet(tokenId, accountId)
    if (isAssociated) {
      const transferTx = transferHbar(accountId, ownId);
      try {
        const hbartransferReponse = await sendTransaction(transferTx);
        if (hbartransferReponse) {
          const tokenMinTx = transferNFT(tokenId, accountId, ownId);
          try {
            const tokenMintResponse = await sendNftTransaction(tokenMinTx);
            if (tokenMintResponse) {
              setMinted(!minted)
              Sendnft(nfts)
            }
          } catch (err) {
            console.log(err)
            const retransfer = transferHbar(ownId, accountId)
            try {
              const hbarretransferResponse = await sendTransaction(retransfer);
              console.log(hbarretransferResponse)
            } catch (err) {
              console.log(err);
            }
          }
        }
      } catch (err) {
        console.log(err)
      }

    } else {
      const associateTx = associateNFT(accountId);

      try {
        const associateNFTResponse = await sendTransaction(associateTx);

        if (associateNFTResponse) {
          const transferTx = transferHbar(accountId, ownId);
          try {
            const hbartransferReponse = await sendTransaction(transferTx);
            if (hbartransferReponse) {
              const tokenMinTx = transferNFT(tokenId, accountId, ownId);
              try {
                const tokenMintResponse = await sendNftTransaction(tokenMinTx);
                if (tokenMintResponse) {
                  setMinted(!minted)
                  Sendnft(nfts)
                }
              } catch (err) {
                console.log(err)
                const retransfer = transferHbar(ownId, accountId)
                try {
                  const hbarretransferResponse = await sendTransaction(retransfer);
                  console.log(hbarretransferResponse)
                } catch (err) {
                  console.log(err);
                }
              }
            }
          } catch (err) {
            console.log(err)
          }
        }
      } catch (err) {
        console.log(err);
      }

    }





  }

  // function mintToken(tokenId, accountId, cids) {

  //   const txID = TransactionId.generate(ownId);

  //   const mintTx = new TokenMintTransaction()
  //     .setTransactionId(txID)
  //     .setTokenId(tokenId)
  //     .setMaxTransactionFee(500000)
  //     .setNodeAccountIds([new AccountId(3)])
  //     .setMetadata([Buffer.from(cids)])
  //     .freeze();

  //   return mintTx;
  // }



  async function sendTransaction(tx, sign = false) {
    let response, hashConnectTxBytes;
    console.log(tx)
    hashConnectTxBytes = sign ? (
      makeBytes(tx, accountId)
    ) : (
      tx.toBytes()
    );

    response = await hash?.sendTransaction(
      topic,
      {
        topic: topic,
        byteArray: hashConnectTxBytes,
        metadata: {
          accountToSign: accountId,
          returnTransaction: false,
        }
      }
    )

    if (response?.receipt) {
      return TransactionReceipt.fromBytes(
        response.receipt
      )
    } else {
      throw new Error("No transaction recipt found")
    }
  }
  async function sendNftTransaction(tx, sign = false) {
    let response, hashConnectTxBytes;
    console.log(tx)
    hashConnectTxBytes = sign ? (
      makeBytes(tx, ownId)
    ) : (
      tx.toBytes()
    );

    response = await hash?.sendTransaction(
      topic,
      {
        topic: topic,
        byteArray: hashConnectTxBytes,
        metadata: {
          accountToSign: ownId,
          returnTransaction: false,
        }
      }
    )

    if (response?.receipt) {
      return TransactionReceipt.fromBytes(
        response.receipt
      )
    } else {
      throw new Error("No transaction recipt found")
    }
  }
  function makeBytes(trans, signingAcctId) {
    const transId = TransactionId.generate(signingAcctId);

    trans.setTransactionId(transId);
    trans.setNodeAccountIds([new AccountId(3)]);

    trans.freeze();

    const transBytes = trans.toBytes();

    return transBytes
  }


  function transferNFT(tokenId, accountId, ownId) {
    const txID = TransactionId.generate(ownId);
    console.log(serialNumber)
    const transferTransaction = new TransferTransaction()
      .setTransactionId(txID)
      .addNftTransfer(tokenId, serialNumber, ownId, accountId)
      .setNodeAccountIds([new AccountId(3)])
      .freeze()

    return transferTransaction
  }

  function transferHbar(accountId, ownId) {
    const txID = TransactionId.generate(accountId);

    const transferTransaction = new TransferTransaction()
      .setTransactionId(txID)
      .addHbarTransfer(accountId, new Hbar(-price))
      .addHbarTransfer(ownId, new Hbar(price))
      .setNodeAccountIds([new AccountId(3)])
      .freeze()


    return transferTransaction;
  }

  function associateNFT(accountId) {
    const txID = TransactionId.generate(accountId);
    const tokenIds = [tokenId];
    console.log(tokenIds)
    const associateTransaction = new TokenAssociateTransaction()
      .setTransactionId(txID)
      .setTokenIds(tokenIds)
      .setAccountId(accountId)
      .setNodeAccountIds([new AccountId(3)])
      .freeze();

    return associateTransaction;
  }

  return (

    <motion.div
      initial={{ opacity: 0 }}
      animate={{ opacity: 1, delay: 1 }}
      transition={{
        ease: "easeInOut",
        duration: 0.7,
        delay: 0.15,
      }}
      style={{ border: "1px solid #313f4a", borderRadius: "10px", boxShadow: "2px 2px 5px #00000040", padding: "20px 15px", position: "relative" }}

    >

      <div
        className="bg-secondary-light cursor-pointer  
                        hover:shadow-xl mb-10 mr-4 ml-4 rounded-xl shadow-lg sm:mb-0 m-1"
      >
        {console.log(serialNumber)}
        <Link
          to={`/buy/` + ownId + `/` + tokenId + `/` + name + `/` + price + `/` + serialNumber}
          aria-label="Single Project"

        >
          <div className="mt-16">
            <img
              src={"https://ipfs.io/ipfs/" + image.slice("7")}
              className="2xl:h-72 h-36 lg:h-44 md:h-48 object-cover rounded-xl sm:h-62 w-full xl:h-56"
              alt="Single Project"
            />
          </div>
          <div className="px-4 py-2">
            <p
              className="dark:text-white font-bold font-general-medium mb-3 mt-3
                            md:text-xl text-center lg:text-2xl text-ternary-dark text-lg"
            >
              {name}
            </p>
            <div className="text-black text-center lg:text-xl mb-2">
              {price}HBAR
            </div>


          </div>
        </Link>
        {accountId ? (
          <motion.button
            initial={{ opacity: 0 }}
            animate={{ opacity: 1 }}
            transition={{
              ease: "easeInOut",
              duration: 0.9,
              delay: 0.1,
            }}
            //   className="cursor-pointer bg-transparent bg-gradient-to-r from-purple-700 to-pink-600
            // hover:text-gray-300 px-8 rounded-md"
            style={{
              width: "100%",
              borderRadius: "10px",
              display: "flex",
              fontSize: "18px",
              fontStyle: "normal",
              fontWeight: "600",
              lineHeight: "22px",
              padding: "10px 30px",
              textAlign: "center",
              textShadow: "0 0 4px #0006",
              // transition: ".5s ease",
              // marginBottom: "",
              backgroundColor: "#080810",
              alignItems: "center",
              justifyContent: "center"
            }}
            onClick={() => handleClickMint(tokenId, cid)}
          >
            <motion.h1
              initial={{ opacity: 0 }}
              animate={{ opacity: 1 }}
              transition={{
                ease: "easeInOut",
                duration: 0.9,
                delay: 0.1,
              }}
              className="font-general-regular 
                            text-center text-white
                            font-black
                            mt-1  
                            "
            >
              BUY NOW
            </motion.h1>
          </motion.button>
        ) : (
          <></>
        )}

      </div>

    </motion.div>
  );
};

export default NftSingle;
