import { useState, useContext } from "react";
import { Link } from "react-router-dom";
import { motion } from "framer-motion";

import { AccountContext } from "../../App";
import useThemeSwitcher from "../../hooks/useThemeSwitcher";
import { HashConnect } from "hashconnect";

import { CopyToClipboard } from "react-copy-to-clipboard";
import { Modal } from "antd";
import { LogoutOutlined, CopyOutlined, CheckOutlined } from "@ant-design/icons";

import logoDark from "../../assets/images/v3-removebg-preview-12.png";

let appMetadata = {
  name: "Unreal Integration NFT marketplace",
  description: "Unreal Integration NFT marketplace",
  icon: "https://ipfs.io/ipfs/QmWaYa4TdA67fmr2NrPeiincWFTeLEY7phKwND1pkneGVP"
}

let hashconnect = new HashConnect();

const Navbar = () => {
  const { accountId, setAccountId, setHash, topic, setTopic, setInventory, buy } = useContext(AccountContext);

  const [pairingString, setPairingString] = useState(null)

  //Conenct HashPack Wallet
  async function connectHedera() {
    console.log("connecthedera")
    let initData = await hashconnect.init(appMetadata, "testnet", false);
    console.log(initData)
    setPairingString(initData.pairingString)
    setTopic(initData.topic)
    hashconnect.foundExtensionEvent.once((walletMetadata) => {
      hashconnect.connectToLocalWallet(pairingString, walletMetadata);
      console.log("Success to get walletmetadata");
    })

    hashconnect.pairingEvent.once((pairingData) => {
      console.log("Success to get PairingData", pairingData);
      console.log(pairingData)
      console.log("HashPack accountId: ", pairingData.accountIds[0]);
      setAccountId(pairingData.accountIds[0])
    })

    setHash(hashconnect)

  }

  const [activeTheme] = useThemeSwitcher();
  const [isModalOpen, setIsOpenModal] = useState(false);
  const [isCopied, setIsCopied] = useState(false);

  const openModal = () => {
    setIsOpenModal(true)
  }

  const onCopyText = () => {
    setIsCopied(true);
    setTimeout(() => {
      setIsCopied(false);
    }, 1000);
  };
  console.log("hashconnectL ", hashconnect, topic)

  async  function Disconnect() {
    console.log("disconnect")
    await hashconnect.disconnect(topic);
    setPairingString(null)
    window.location = window.location
    console.log(hashconnect)

    setHash(hashconnect)
  }

  return (
    <motion.nav
      initial={{ opacity: 0 }}
      animate={{ opacity: 1 }}
      id="nav"
      className="sm:container sm:mx-auto">
      <div className="grid grid-cols-4 py-2 sm:flex sm:items-center sm:justify-between xl:max-w-screen-xl z-10">
        <div className="flex items-center justify-between px-4 sm:px-0">
          <div>
            {activeTheme === "dark" ? (
              <Link to="/">
                <div className="flex ">
                  {buy ? <Link to="/"><img src={logoDark} className="w-10 sm:w-20" alt="Light Logo" /></Link> : <img src={logoDark} className="w-10 sm:w-20" alt="Light Logo" onClick={() => setInventory(false)} style={{ cursor: "pointer" }} />}
                </div>
              </Link>
            ) : (
              <div className="flex">
                {buy ? <Link to="/"><img src={logoDark} className="w-20" alt="Light Logo" /></Link> : <img src={logoDark} className="w-20" alt="Light Logo" onClick={() => setInventory(false)} style={{ cursor: "pointer" }} />}
              </div>
            )}
          </div>


        </div>

        <Link to="/marketplace"><h3 className="text-black text-xs sm:text-2xl font-bold text-right item-center justify-center md:pr-6 mt-4 " >MarketPlace</h3></Link>
        <Link to="/inventory"><h3 className="text-black text-xs sm:text-2xl font-bold text-right item-center justify-center pr-6 mt-4 sm:mr-8">Inventory</h3></Link>


        <div className="flex-col  items-center justify-between md:flex-row sm:flex">



          <div className=" w-20 h-5 mt-3 sm:flex sm:w-40 sm:h-10 sm:mt-4" style={{ transform: "skew(-35deg, 0deg)", backgroundColor: "#FF0000", alignItems: "center", justifyContent: "center" }}>

            {!accountId ? (
              <button className="flex font-bold text-white text-xs sm:text-lg text-center pt-0.5 px-5 " style={{ transform: "skew(35deg, 0deg)" }} onClick={() => connectHedera()}>
                Wallet
              </button>
            ) : (
              <button
                className="flex font-bold text-white text-xs text-center px-1 pt-0.5"
                style={{ transform: "skew(35deg, 0deg)" }}
                onClick={Disconnect}
              >
                {accountId.toString()}
              </button>
            )}

          </div>
        </div>
      </div>

      <Modal
        open={isModalOpen}
        footer={null}
        onCancel={() => setIsOpenModal(false)}
        closable={false}
        width={400}
      >
        <div className="flex justify-center mb-4 w-full">

          <div className="bg-ternary-dark border border-gray-700 flex p-2 rounded-lg text-gray-300">
            <span className="pr-2">
              {" "}
              {accountId ? <> {accountId}</> : <></>}
            </span>
            <CopyToClipboard text={accountId} onCopy={onCopyText}>
              <div className="border-gray-700 border-l copy-area hover:text-gray-600 pl-3">
                {!isCopied ? (
                  <CopyOutlined style={{ color: "white" }} />
                ) : (
                  <CheckOutlined style={{ color: "white" }} />
                )}
              </div>
            </CopyToClipboard>
          </div>
        </div>

        <div className="flex justify-end w-full">
          <button
            className="flex h-50 bg-transparent dark:border-2 bg-gradient-to-r 
                        hover:text-gray-400
                        from-purple-700 to-pink-600
                          duration-300 font-general-medium 
                          rounded-md shadow-sm text-md text-white font-bold text-sm p-2"
            onClick={Disconnect}
            >
            Disconnect
            <div className="flex justify-center pt-1 w-full">
              <LogoutOutlined className="pl-1" />
            </div>
          </button>
        </div>

      </Modal>

    </motion.nav>
  );
};

export default Navbar;